.imgModal {
  max-width: calc(100vw - 148px);
  max-height: calc(100vh - 124px);
  object-fit: cover;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.modal {
  max-width: calc(100vw - 148px);
  max-height: calc(100vh - 124px);
  overflow: hidden;
}
